
* {
  margin: 0;
  padding: 0;
  line-height: 1;
  list-style: none;
}
html,
body {
  width: 50%;
  height: 30%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-color: #bbbbbb;
}
.canvasArea {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: absolute;
  border-radius: 10px;
  -webkit-box-shadow: 10px 10px 20px #cccccc;
          box-shadow: 10px 10px 20px #cccccc;
  background-color: #001d2e;
  width: 640px;
  height: 480px;
  padding: 10px 5px;
}
#bgCanvas,
#symCanvas,
#starCanvas,
#gameCanvas {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}
#gameCanvas {
  z-index: 5;
}
#bgCanvas {
  z-index: 5;
}
#symCanvas {
  z-index: 2;
}
#starCanvas {
  z-index: 1;
}

.btn {
  z-index: 6;
  margin-top: 100px;
}
.btn a {
    padding: 8px 32px;
    text-decoration: none;
    color: #fff;
    border: 1px solid #fff;
    border-radius: 20px;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
}
.btn a:hover {
      color: #001d2e;
      background-color: #fff;
}
.wave {
  width: 328px;
  height: 328px;
  border-radius: 50%;
  border: 1px solid rgba(255, 255, 255, 0.5);
  position: absolute;
  top: 18%;
  left: 50%;
  margin-left: -165px;
  text-align: center;
  -webkit-animation: wave 3s linear infinite;
          animation: wave 3s linear infinite;
  opacity: 0;
  z-index: 4;
}
.wave-2 {
  -webkit-animation-delay: 750ms;
  animation-delay: 750ms;
}
.wave-3 {
  -webkit-animation-delay: 1500ms;
  animation-delay: 1500ms;
}
@-webkit-keyframes wave {
0% {
    -webkit-transform: scale();
            transform: scale();
    opacity: 1;
    filter: alpha(opacity=100) \9;
}
100% {
    -webkit-transform: scale(2);
            transform: scale(2);
    opacity: 0;
    filter: alpha(opacity=0) \9;
}
}
@keyframes wave {
0% {
    -webkit-transform: scale();
            transform: scale();
    opacity: 1;
    filter: alpha(opacity=100) \9;
}
100% {
    -webkit-transform: scale(2);
            transform: scale(2);
    opacity: 0;
    filter: alpha(opacity=0) \9;
}
}
